import React, { useState, useEffect } from "react";
import "./FavCard.css";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import {
  addToFavList,
  remFromFavList,
  getFavList,
} from "../../actions/movieAction";
import { useAlert } from "react-alert";
import { Fragment } from "react";


const MovieCard = ({ movie,favTab=false }) => {
  const { isAuthenticated, user } = useSelector((state) => state.user);
  const { favList } = useSelector((state) => state.UserFavList);
  const dispatch = useDispatch();


  const alert = useAlert();
  const [inFavList, setInFavList] = useState(false);
  const imgURL = movie? `https://www.themoviedb.org/t/p/w300_and_h450_bestv2${movie.poster_path}`:''

  useEffect(() => {
    if (isAuthenticated === true) {
      dispatch(getFavList(user._id));
    }
  }, [user, isAuthenticated, dispatch, alert]);

  const addtoFav = (id) => {
    setInFavList(true);
    
     dispatch(addToFavList(user._id, id));
    alert.success("Added to favorites");
  };

  const remFromFav = (e,id) => {

    setInFavList(false);
    dispatch(remFromFavList(user._id, id));
    alert.success("Removed from favorites");
  };
  useEffect(() => {
      if(isAuthenticated){
      favList.UserFavList.favList.map((favitem) => {
        if (favitem.id === movie.id) {
          setInFavList(true);
        }
      });
    }
  }, [movie,isAuthenticated,favList]);

  return (
    <Fragment>
      {inFavList ? (
        <div key={movie.id} className="cardContainer">
          <Link to={`/${movie.id}/details/${movie.title}`}>
            <div className="movieCard">
              <div>
                <img dragable="false" src={imgURL} alt={movie.title} />
              </div>
              <div className="info">
                <h1>{movie.title}</h1>
                <span>{movie.vote_average}</span>
              </div>

              <p>{movie.release_date}</p>
            </div>
          </Link>
          {inFavList ? (
            (
              <button
                onClick={(e) => remFromFav(e,movie.id)}
                style={{ background: "red" }}
                id={movie.id}
                className="addToFav"
              >
                Remove
              </button>
            )
          ) :  (
            <button onClick={() => addtoFav(movie.id)} className="addToFav">
              +Add to Favorites
            </button>
          )}
        </div>
      ) : (
        ""
      )}
    </Fragment>
  );
};

export default MovieCard;
